import tuiNumberbox from "@/components/numberbox/numberbox"
export default {
	components:{
		tuiNumberbox
	},
	data() {
		return {
			value: 0,
			value2: 1,
			value3: 1,
			value4: 0,
			value5: 1,
			value6: 1,
			value7: 1
		}
	},
	methods: {
		change: function(e) {
			this.value = e.value
		},
		change2: function(e) {
			this.value2 = e.value
		},
		change3: function(e) {
			this.value3 = e.value
		},
		change4: function(e) {
			this.value4 = e.value
		},
		change5: function(e) {
			this.value5 = e.value
		},
		change6: function(e) {
			this.value6 = e.value
		},
		change7: function(e) {
			this.value7 = e.value
		}
	}
}
